<template>

<div class="comicCC">
  <div class="nav">
    <span>ComicName ></span>
    <select name="" id="">
      <option value="1">Chapter 1</option>
      <option value="2">Chapter 2</option>
    </select>

    <select name="" id=""  v-model="nowPage">
      <option value="1">Page 1</option>
      <option value="2">Page 2</option>
      <option value="3">Page 3</option>
      <option value="4">Page 4</option>
      <option value="5">Page 5</option>
      <option value="6">Page 6</option>
      <option value="7">Page 7</option>
      <option value="8">Page 8</option>
      <option value="9">Page 9</option>
      <option value="10">Page 10</option>
    </select>
  </div>
  <div class="comicPage">
    <div class="lbtn" @click="prePage"> &lt;  </div>
    <div class="page"><img :src="showImg" alt=""></div>
    <div class="rbtn" @click="nextPage"> ></div>
  </div>
  <div class="footer">
    <div class="btnn" @click="prePage"> &lt;  </div>
    <div class="footer">

    <div v-for="item in 10" :class="{selected: item === nowPage}" :key="item">
      <img class="foo"  :src="footerImg(item)" alt="">
    </div>

  </div>
    <div class="btnn" @click="nextPage"> ></div>
  </div>


</div>
</template>
<script>
export default {
  name: 'Comic',
  data() {
    return {
      chapter: null,
      nowPage: 1,
      options: [
        { text: 'One', value: 'A' },
        { text: 'Two', value: 'B' },
        { text: 'Three', value: 'C' },
      ],
    };
  },
  methods: {
    prePage() {
      if (this.nowPage > 1) {
        this.nowPage -= 1;
      }
    },
    nextPage() {
      if (this.nowPage < 10) {
        this.nowPage += 1;
      }
    },
    GetNowpage() {
      const re = this.$route.query.num;
      this.nowPage = Number(re);
    },
    footerImg(num) {
      return `https://hexschool.github.io/THE_F2E_Design/week5-comic%20viewer/assets/storyboard-${num}.png`;
    },
  },
  created() {
    this.GetNowpage();
  },
  watch: {
    nowPage(val) {
      console.log(val);
    },
  },
  computed: {
    showImg() {
      if (this.nowPage !== undefined) {
        const num = this.nowPage;
        return `https://hexschool.github.io/THE_F2E_Design/week5-comic%20viewer/assets/storyboard-${num}.png`;
      }
      const num = this.$route.query.num;
      return `https://hexschool.github.io/THE_F2E_Design/week5-comic%20viewer/assets/storyboard-${num}.png`;
    },
  },
};
</script>
<style>
::-webkit-scrollbar {
width: 10px;
height: 10px;
border: 3px solid #000;
}
::-webkit-scrollbar-track {
-webkit-border-radius: 10px;
border-radius: 10px;
}
::-webkit-scrollbar-thumb {
-webkit-border-radius: 4px;
border-radius: 4px;
background: #000;
}
::-webkit-scrollbar-button{
-ms-scrollbar-arrow-color: #fff;
-ms-scrollbar-face-color: #777;
background: #000;
}
.lbtn,
.rbtn {
  width: 60px;
  font-size: 40pt;
  font-weight: bold;
  display: flex;
  align-items: center;
}
.lbtn:hover,
.rbtn:hover {
  background: #000;
  color: #50ff44;
}
.ter {
  align-items: center;
}
.btnn {
  width: 60px;
  font-size: 40pt;
  font-weight: bold;
  display: flex;
  align-items: center;
}
.btnn:hover {
  background: #50ff44;
  color: #000;
}
.comicCC {
  flex-direction: column;
  width: 620px;
  display: flex;
  margin: auto;
  align-items: center;
}
.nav {
  font-weight: bold;
  margin: 20px 0;
}
.page {
  max-width: 620px;
}
.page > img {
  max-width: 100%;
}
.comicPage {
  display: flex;
}
.footer {
  display: flex;
  padding: 20px 0;
  width: 620px;
  overflow: auto;
}
.selected {
  border: 2px solid #000;
}
.foo {
  width: 70px;
}
</style>




