import Vue from 'vue';
import Router from 'vue-router';
import Main from '@/components/Main';
import Comic from '@/components/Comic';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      component: Main,
    },
    {
      path: '/Main',
      component: Main,
    },
    {
      path: '/Comic',
      component: Comic,
    },
  ],
});




