<template>
<div class="body">
   <div class="ad">
      <img src="https://hexschool.github.io/THE_F2E_Design/week5-comic%20viewer/assets/ad-1.png" alt="">
   </div>
   <div class="comic">
      <div class="l"><img src="https://hexschool.github.io/THE_F2E_Design/week5-comic%20viewer/assets/comic%20cover.png" alt=""></div>
      <div class="r">
         <div class="r-title">MY HEXSCHOOL</div>
         <div class="info">
            <div class="comicInfo">
               <div >
                  <strong>Genres</strong>
                  Fusce/vehicula/dolor
               </div>
               <div>
                  <strong>Author</strong>
                  Namae Shiranai
               </div>
               <div>
                  <strong>Status</strong>
                  Ongoing
               </div>
               <div>
                  <strong>Rate</strong>
                  ★★★★☆
               </div>
            </div>
            <div class="comicSum">
               <div class="sumTitle">Summary</div>
               <div>If your banker breaks, you snap; if your apothecary
                  by mistake sends you poison in your pills, you die.
               </div>
               <div class="mt-2">Therefore, I say, I saw that this situation of mine was the precise
                  situation of every mortal that has this Siamese connexion with
                  a plurality of other mortals.
               </div>
            </div>
         </div>
      </div>
   </div>
   <div class="ad">
     <img src="https://hexschool.github.io/THE_F2E_Design/week5-comic%20viewer/assets/ad-2.png" alt="">
   </div>
   <div class="chapters">
     <div class="c_title">All Chapters</div>
     <div class="chapter" >
       <div class="item" v-for="(item,index) in comic" :key="item.id">
       <div @click="toComic(1)">{{item.name}} <span class="new"
        v-if="(index+1) >= comic.length">New</span> </div>
       </div>

     </div>
   </div>
   <div class="ad">
     <img src="https://hexschool.github.io/THE_F2E_Design/week5-comic%20viewer/assets/ad-3.png" alt="">
   </div>
</div>
</template>
<script>
export default {
  name: 'Main',
  data() {
    return {
      comic: [{
        id: 1,
        name: 'Chapter 1: The F2E Challenge Start!',
      },
      {
        id: 2,
        name: 'Chapter 2: Todo List is Going Crazy!',
      }],
    };
  },
  methods: {
    toComic(num) {
      this.$router.push(`/comic?num=${num}`);
    },
  },
  created() {},
};
</script>

<style>
.body {
  margin: 0 10% 0 10%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.ad {
  padding: 24px 0;
}
.comic {
  min-width: 620px;
  max-width: 620px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.l,
.r {
  flex: 1;
}
.l {
  border: 5px solid #000;
}
.l > img {
  max-width: 100%;
}
.r-title {
  font-size: 18pt;
  background: #000;
  color: #fff;
}
.r > .r-title {
  letter-spacing: 8px;
  text-align: center;
}
.r strong {
  margin-right: 10px;
}
.info div {
  margin: 7px 0 7px 5%;
  font-size: 14pt;
}
.sumTitle {
  font-size: 22pt;
  margin-top: 40px;
}
.c_title{
  background: #000;
  color: #fff;
  width: 30%;
  font-size: 18pt;
  text-align: center;
  font-weight: bold;
}
.chapters{
  width: 620px;
}
.chapter{
  background: #fff;
  border: 5px solid #000;
  }
.chapter>.item{
  padding: 5px;
  font-weight: bold;
}
.chapter>.item:hover{
  background: #000;
  color: #fff;
  cursor: pointer;
}
.new{
  background: #50ff44;
}
.item:hover .new{
  color: #000 !important;
}
</style>



