<template>
<div id="app">
  <div class="header">
    <div @click="toMain">Comicomic</div>
  </div>
    <router-view></router-view>
</div>


</template>
<script>
export default {
  name: 'App',
  data() {
    return {};
  },

  methods: {
    toMain() {
      this.$router.push('/main');
    },
  },
};
</script>


<style>
.header {
  background: black;
  color: #50ff44;
  display: flex;
  font-size: 28pt;
  font-weight: bold;
  justify-content: center;
}
#app{
  background: #e2e2e2;
}
</style>



